@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap);
body {
  margin: 0;
  font-family: 'Noto Sans KR', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background-color: #eff2f7; */
}

* {
  box-sizing: border-box;
}

/* code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
} */

.checkDiv {
  position: relative;
}

input[type='checkbox'] {
  display: none;
}

input[type='checkbox'] + label {
  font-size: 18;
  color: #616161;
  cursor: pointer;
}

input[type='checkbox'] + label span {
  display: inline-block;
  width: 15px;
  height: 15px;
  margin: -2px 10px 0 0;
  vertical-align: middle;
  background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAB4AAAAeCAYAAAA7MK6iAAAABHNCSVQICAgIfAhkiAAAAFNJREFUSEvtl7ERACAIA2FdBmJdrbSxsYimeQYIl/ApyKoaYZi0L+7u/GF8Gd2OWfwqdqIO4AIuVQLUiTqpWDp0gAu4gEuWAHXy1Ul2xEsh2+80AWRSq0UzQJz6AAAAAElFTkSuQmCC) no-repeat 0 0px / contain;
}

input[type='checkbox']:checked + label span {
  background: url(data:image/png;base64,) no-repeat 0 0px / contain;
}

